<template>
  <MainLayout>
    <div class="content">
      <div class="wrapper">
        <div class="form">
          <div class="form-group">
            <label for="name">Name</label>
            <input
              type="text"
              class="form-control"
              id="name"
              required
              v-model="employee.name"
              name="name"
            />
          </div>
          <div class="form-group">
            <label for="description">Surname</label>
            <input
              type="text"
              class="form-control"
              id="surname"
              required
              v-model="employee.surname"
              name="surname"
            />
          </div>
          <div class="form-group">
            <label for="description">Email</label>
            <input
              type="text"
              class="form-control"
              id="email"
              required
              v-model="employee.email"
              name="email"
            />
          </div>
          <div class="form-group">
            <label for="description">Password</label>
            <input
              type="text"
              class="form-control"
              id="password"
              required
              v-model="employee.password"
              name="password"
            />
          </div>
          <div class="form-group">
            <label for="description">Phone</label>
            <input
              type="number"
              class="form-control"
              id="phone"
              required
              v-model="employee.phone"
              name="phone"
            />
          </div>
          <div class="form-group">
            <select v-model="role">
              <option disabled value="">Select role</option>
              <option>Dyspozytor</option>
              <option>Admin</option>
            </select>
          </div>
          <button @click="saveEmployee" class="btn btn-success">Submit</button>
        </div>
      </div>
    </div>
    <Teleport to="body">
      <Message
        :messageType="messageType"
        :show="showMessage"
        :message="messageContent"
        :closeMessage="closeMessage"
      />
    </Teleport>
  </MainLayout>
</template>
<script>
import DataService from "../services/DataService";
import { onValue } from "firebase/database";
import { createUserWithEmailAndPassword, getAuth } from "firebase/auth";
import Message from "./Message";
import MainLayout from "./MainLayout";
export default {
  name: "add-employee",
  components: { MainLayout, Message },
  data() {
    return {
      employee: {
        name: "",
        surname: "",
        email: "",
        password: "",
        id: "",
        phone: "",
        role: "",
      },
      users: [],
      role: "",
      submitted: false,
      alert: "",
      showMessage: false,
      messageType: "",
      messageContent: "",
    };
  },
  methods: {
    printMessage(type, message) {
      this.showMessage = true;
      this.messageType = type;
      this.messageContent = message;
    },
    closeMessage() {
      this.showMessage = false;
    },
    saveEmployee() {
      if (this.validate()) {
        this.capitalizeFirstLetter();
        var data = {
          name: this.employee.name,
          surname: this.employee.surname,
          email: this.employee.email,
          id: this.setId(),
          phone: this.employee.phone,
          role: this.role,
        };

        const auth = getAuth();
        createUserWithEmailAndPassword(
          auth,
          this.employee.email,
          this.employee.password
        )
          .then(() => {
            DataService.createEmployee(data)
              .then(() => {
                //console.log("Created employee successfully!");
                this.submitted = true;
                //alert(this.alert);
                this.printMessage("success", this.alert);
                this.newEmployee();
              })
              .catch((e) => {
                this.printMessage(
                  "danger",
                  "Error occured, report it to administrator!"
                );
                console.log(e);
              });
          })
          .catch((err) => {
            this.printMessage(
              "danger",
              "Error occured, report it to administrator!"
            );
            console.log(err);
          });
      } else {
        this.printMessage("warning", this.alert);
        //alert(this.alert)
      }
    },
    capitalizeFirstLetter() {
      this.employee.name =
        this.employee.name.charAt(0).toUpperCase() +
        this.employee.name.slice(1);
      this.employee.surname =
        this.employee.surname.charAt(0).toUpperCase() +
        this.employee.surname.slice(1);
      if (this.employee.surname.indexOf("-") >= 0) {
        this.employee.surname =
          this.employee.surname.slice(
            0,
            this.employee.surname.indexOf("-") + 1
          ) +
          this.employee.surname
            .charAt(this.employee.surname.indexOf("-") + 1)
            .toUpperCase() +
          this.employee.surname.slice(this.employee.surname.indexOf("-") + 2);
      }
    },
    setId() {
      let id;
      if (this.users.length === 0) {
        id = 1;
      } else {
        let lastID = this.users[this.users.length - 1].id;
        id = lastID.replace("ID", "");
        id++;
        //console.log(this.users[this.users.length-1].id);
        //console.log(id);
        //zmienic na pobieranie tylko ostatniego usera i sprawdzenie jego id;
      }
      const newId = "ID" + id;
      //console.log(newId);
      return newId;
    },
    newEmployee() {
      this.employee = {
        name: "",
        surname: "",
        email: "",
        password: "",
        id: "",
        phone: "",
        role: "",
      };
    },
    validate() {
      this.alert = "";
      if (this.employee.name == "") {
        this.alert += "Name cannot be empty. ";
      }
      if (!/^[A-Za-ząęśżźćółńĄĘŚŻŹĆÓŁŃ]+$/.test(this.employee.name)) {
        this.alert += "Name can contain only letters from polish alphabet. ";
      }
      if (this.employee.surname == "") {
        this.alert += "Surname cannot be empty. ";
      }
      if (!/^[A-Za-ząęśżźćółńĄĘŚŻŹĆÓŁŃ-]+$/.test(this.employee.surname)) {
        this.alert +=
          "Surname can contain only letters from polish alphabet and - in case of double surname. ";
      }
      if (this.employee.email == "") {
        this.alert += "Email cannot be empty. ";
      }
      for (let i = 0; i < this.users.length; i++) {
        //console.log(this.employee.email === this.users[i].email);
        if (this.employee.email === this.users[i].email) {
          this.alert += "Email already in use. ";
          break;
        }
      }
      if (this.employee.password == "") {
        this.alert += "Password cannot be empty. ";
      } else if (this.employee.password.length < 8) {
        this.alert += "Password must be longer than 8 characters. ";
      }
      if (this.employee.phone == "") {
        this.alert += "Phone cannot be empty. ";
      } else if (
        this.employee.phone.toString().length < 9 ||
        this.employee.phone.toString().length > 11
      ) {
        this.alert += "Phone number has to have 9 to 11 characters. ";
      }
      if (this.role == "") {
        this.alert += "Select role for new employee. ";
      }
      if (this.alert === "") {
        this.alert += "Employee created successfully!";
        return true;
      }
      return false;
    },
    onDataChange(items) {
      let _users = [];
      items.forEach((item) => {
        let data = item.val();
        _users.push({
          key: item.key,
          id: data.id,
          email: data.email,
        });
      });
      this.users = _users;
    },
  },
  mounted() {
    let data;
    // onValue(DataService.getAll(), (snapshot) => {
    //   data = snapshot;
    //   this.onDataChange(data);
    // });
    onValue(DataService.getFromPath("/employees"), (snapshot) => {
      data = snapshot;
      this.onDataChange(data);
    });
  },
};
</script>
<style scoped>
.content {
  overflow-y: auto;
  display: flex;
}

.wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 1rem;
  padding: 1rem;
  gap: 1rem;
  background-color: #b1b1b1;
  border-radius: 0.5rem;
  overflow-y: auto;
  box-shadow: 0px -1px 10px 4px #141414a6;
}
.form {
  width: 60%;
}
</style>
